<script>
export default {
  name: 'MenuItem',
  functional: true,
  props: {
    icon: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    }
  },
  render (h, context) {
    const { icon, title } = context.props
    const vnodes = []
    // console.log(icon)
    if (icon) {
      /* 使用element图标 */
      // vnodes.push(
      //   h('i', {
      //     class: `iconfont ${icon}`
      //   })
      // )
      /* 使用自带的svg图 */
      vnodes.push(<svg-icon icon-class={icon} />)
    }

    if (title) {
      vnodes.push(<span slot="title">{title}</span>)
    }
    return vnodes
  }
}
</script>



