import moment from 'moment'
import dayjs from 'dayjs'

/**
 * 获取当前时间
 * @author Demon3443002624@qq.com
 * @param {String} 'YYYY年M月D日 周N HH:mm:ss'
 */
export const getWeekTime = (format = `YYYY年M月D日 周 HH:mm:ss`) => {
  const weekStr = format.split(` `)[1]
  const myEnum = ['日', '一', '二', '三', '四', '五', '六']
  const weekday = moment().format('e')
  const nowTime = moment()
    .format(format)
    .replace(weekStr, weekStr + myEnum[weekday])
  return nowTime
}
/**
 * 格式化后端传输时间
 * @author Demon3443002624@qq.com
 * @param {String} '/Date(1637294420000)/'
 * @returns {moment}
 */
export const formatTime = (timeStr) => {
  return moment(new Date(Number(timeStr.replace(`/Date(`, '').replace(`)/`, ''))))
}
export const formatTimeDayjs = (timeStr) => {
  return dayjs(new Date(Number(timeStr.replace(`/Date(`, '').replace(`)/`, ''))))
}
/**
 * GPS（谷歌地图）、GCj、BD-09（百度地图）坐标系之间的互转
 * @author Demon3443002624@qq.com
 * @returns {Object} {Longitude,Latitude} {经度、维度}
 */
const $scope = new Object()
// GPS->GCj
// GPS转GCj定位https://my.oschina.net/Thinkeryjgfn/blog/402565
$scope.gps_gcj02_encrypt = function (lat, lon) {
  if ($scope.outOfChina(lat, lon)) {
    var out = []
    out.latitude = lat
    out.longitude = lon
    return out
  }
  var pi = 3.14159265358979324
  var a = 6378245.0 //  a: 卫星椭球坐标投影到平面地图坐标系的投影因子。
  var ee = 0.00669342162296594323 //  ee: 椭球的偏心率。
  var dLat = $scope.transformLat(lon - 105.0, lat - 35.0)
  var dLon = $scope.transformLon(lon - 105.0, lat - 35.0)
  var radLat = (lat / 180.0) * pi
  var magic = Math.sin(radLat)
  magic = 1 - ee * magic * magic
  var sqrtMagic = Math.sqrt(magic)
  dLat = (dLat * 180.0) / (((a * (1 - ee)) / (magic * sqrtMagic)) * pi)
  dLon = (dLon * 180.0) / ((a / sqrtMagic) * Math.cos(radLat) * pi)
  var result = {}
  result.Latitude = lat + dLat
  result.Longitude = lon + dLon
  return result
}
$scope.outOfChina = function (lat, lon) {
  if (lon < 72.004 || lon > 137.8347) return true
  if (lat < 0.8293 || lat > 55.8271) return true
  return false
}
$scope.transformLat = function (x, y) {
  var pi = 3.14159265358979324
  var ret = -100.0 + 2.0 * x + 3.0 * y + 0.2 * y * y + 0.1 * x * y + 0.2 * Math.sqrt(Math.abs(x))
  ret += ((20.0 * Math.sin(6.0 * x * pi) + 20.0 * Math.sin(2.0 * x * pi)) * 2.0) / 3.0
  ret += ((20.0 * Math.sin(y * pi) + 40.0 * Math.sin((y / 3.0) * pi)) * 2.0) / 3.0
  ret += ((160.0 * Math.sin((y / 12.0) * pi) + 320 * Math.sin((y * pi) / 30.0)) * 2.0) / 3.0
  return ret
}
$scope.transformLon = function (x, y) {
  var pi = 3.14159265358979324
  var ret = 300.0 + x + 2.0 * y + 0.1 * x * x + 0.1 * x * y + 0.1 * Math.sqrt(Math.abs(x))
  ret += ((20.0 * Math.sin(6.0 * x * pi) + 20.0 * Math.sin(2.0 * x * pi)) * 2.0) / 3.0
  ret += ((20.0 * Math.sin(x * pi) + 40.0 * Math.sin((x / 3.0) * pi)) * 2.0) / 3.0
  ret += ((150.0 * Math.sin((x / 12.0) * pi) + 300.0 * Math.sin((x / 30.0) * pi)) * 2.0) / 3.0
  return ret
}
// GPS->BD-09
// 谷歌转百度
$scope.gps_bd_encrypt = function (lat, lon) {
  const res = $scope.gps_gcj02_encrypt(lat, lon)
  return $scope.gcj02_bd_encrypt(res.Latitude, res.Longitude)
}
// BD-09->GPS
// 百度转谷歌地球
$scope.bd_gps_encrypt = function (lat, lon) {
  var x_pi = (3.14159265358979324 * 3000.0) / 180.0
  var x = lon - 0.0065,
    y = lat - 0.006
  var z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * x_pi)
  var theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * x_pi)
  var gg_lon = z * Math.cos(theta)
  var gg_lat = z * Math.sin(theta)
  var result = { Longitude: gg_lon, Latitude: gg_lat }
  return result
}

// GCj->BD-09
// 谷歌地图经纬度转百度地图经纬度
$scope.gcj02_bd_encrypt = function (gg_lat, gg_lon) {
  if ($scope.outOfChina(gg_lat, gg_lon)) {
    var out = [gg_lon, gg_lat]
    return out
  }
  var x_pi = (3.14159265358979324 * 3000.0) / 180.0
  var x = gg_lon,
    y = gg_lat
  var z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * x_pi)
  var theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * x_pi)
  var bd_lon = z * Math.cos(theta) + 0.0065
  var bd_lat = z * Math.sin(theta) + 0.006
  var result = { Longitude: bd_lon, Latitude: bd_lat }
  return result
}
// GCj->GPS
// GCj转GPS(WGS84)定位
$scope.gcj02_gps_encrypt = function (lng, lat) {
  var pi = 3.14159265358979324
  var ee = 0.00669342162296594323
  var a = 6378245.0
  var dLat = $scope.transformLat(lng - 105.0, lat - 35.0)
  var dLng = $scope.transformLon(lng - 105.0, lat - 35.0)
  var radLat = (lat / 180.0) * pi
  var magic = Math.sin(radLat)
  magic = 1 - ee * magic * magic
  var sqrtMagic = Math.sqrt(magic)
  dLat = (dLat * 180.0) / (((a * (1 - ee)) / (magic * sqrtMagic)) * pi)
  dLng = (dLng * 180.0) / ((a / sqrtMagic) * Math.cos(radLat) * pi)
  var mgLat = lat + dLat
  var mgLng = lng + dLng
  return { Longitude: lng * 2 - mgLng, Latitude: lat * 2 - mgLat }
}
export const coordinateHandler = { ...$scope }
/**
 * 验证上传文件的分辨率比例
 * @author Demon3443002624@qq.com
 * @param {File} file The readAsDataURL method is used to read the contents of the specified Blob or File.
 * @param {Number} proportion width/height
 * @returns {Promise}
 */
export const validatePicProportion = (file, proportion) => {
  return new Promise(function (resolve, reject) {
    try {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => {
        let img = new Image()
        img.src = reader.result
        if (img.complete) {
          if (img.width / img.height !== proportion) {
            resolve(false)
          } else {
            resolve(true)
          }
        } else {
          img.onload = () => {
            if (img.width / img.height !== proportion) {
              resolve(false)
            } else {
              resolve(true)
            }
          }
        }
      }
    } catch (error) {
      reject(error)
    }
  })
}
/**
 * @desc 拼接图片地址
 * @auth Demon3443002624@qq.com
 * @param {String} url
 * @param {*} old
 * @returns
 */
export const handlePicUrl = (url, old) => {
  let res = url
  const baseUrl = 'http://www.fortrustyun.com'
  if (!!old) {
    res = baseUrl + '/' + url
  } else {
    res = process.env.BASE_API + '/' + url
  }
  return res
}
/**
 * 将数字取整为N的倍数
 * @author Demon3443002624@qq.com
 * @param {Number} N 基数
 * @param {Number} num 需要取整的值
 * @param {Boolean} ceil 是否向上取整
 */
export const formatInt = (num, N = 10, ceil = true) =>
  ceil ? Math.ceil(num / N) * N : Math.floor(num / N) * N

// 机组状态返回值 对应显示文本和颜色
export const showByCrewStatus = (i) => {
  // 0离线 1停机  2警告 3报警 4运行
  const color = [
    'rgba(128, 128, 128, 1)',
    'rgba(255, 255, 255, 1)',
    'rgba(255, 246, 0, 1)',
    'rgba(255, 0, 0, 1)',
    'rgba(92, 245, 72, 1)'
  ]
  const text = ['离线', '停机', '警告', '报警', '运行']
  return {
    color: color[i],
    text: text[i]
  }
}



