// 存在组件间循环引用的场景，因此在外部统一注册
import GenerateSheet from './GenerateSheet'

const install = (Vue) => {
  Vue.component(GenerateSheet.name, GenerateSheet)
}

export default {
  install,
  version: '1.0.0'
}



