import Vue from 'vue'
import Router from 'vue-router'
/* Layout */
import Layout from '@/layout'
// 新模块
import projectRouter from './modules/project'
import equipmentRouter from './modules/equipment'
import equipmentArchives from './modules/equipmentArchives'
import operationRouter from './modules/operation'
import resource from './modules/resource'
import setting from './modules/setting'
import businessRouter from './modules/business'
import engineeringRouter from './modules/engineering'
import testRouter from './modules/test'

Vue.use(Router)
// 获取原型对象上的push函数
const originalPush = Router.prototype.push
// 修改原型对象中的push方法
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err)
}

/**
 * constantRoutes
 * a base page that does not have permission requirements
 * all roles can be accessed
 */
export const constantRoutes = [
  {
    path: '',
    redirect: '/login',
    hidden: true
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/login'),
    meta: {
      name: '登录页',
      icon: ''
    },
    hidden: true
  },
  {
    path: '/ms',
    name: 'MonitoringScreen',
    component: () => import('@/views/monitoringScreen'),
    meta: {
      name: '运维监控大屏',
      icon: ''
    },
    hidden: true
  },
  {
    path: '/ms/fs',
    name: 'MonitoringScreenFullscreen',
    component: () => import('@/views/monitoringScreen/components/EchartsMapRotationFs'),
    meta: {
      name: '运维监控大屏-地图全屏',
      icon: ''
    },
    hidden: true
  },
  {
    path: '/abandoned/ms',
    name: 'MonitoringScreenOld',
    component: () => import('@/views/abandoned/monitoringScreen'),
    meta: {
      name: '运维监控大屏（老地图-固定比例缩放）',
      icon: ''
    },
    hidden: true
  },
  {
    path: '/test/msr',
    name: 'MonitoringScreenResponsive',
    component: () => import('@/views/abandoned/monitoringScreenResponsive'),
    meta: {
      name: '运维监控大屏（老地图-响应式布局）',
      icon: ''
    },
    hidden: true
  },
  // this is for test
  {
    path: '/test/testPage',
    name: 'TestPage',
    component: () =>
      import('@/views/monitoringScreen/components/EchartsMapRotation/components/Country'),
    // import('@/views/monitoringScreen/components/EchartsMapRotation'),
    meta: {
      name: '测试页',
      icon: ''
    },
    hidden: true
  },
  {
    path: '/customerMonitoringScreen',
    name: 'CustomerMonitoringScreen',
    component: () => import('@/views/abandoned/screenData/customerMonitoringScreen'),
    meta: {
      name: '客户首页',
      icon: ''
    },
    hidden: true
  },
  {
    path: '/employeeMonitoringScreen',
    name: 'EmployeeMonitoringScreen',
    component: () => import('@/views/abandoned/screenData/employeeMonitoringScreen'),
    meta: {
      name: '员工首页',
      icon: ''
    },
    hidden: true
  },
  {
    path: '/crew-monitor-info-fullscreen',
    name: 'CrewMonitorInfoFullscreen',
    component: () => import('@/views/equipmentManage/crew-monitor/fullscreen'),
    meta: {
      name: '机组监控全屏',
      icon: ''
    },
    hidden: true
  },

  {
    path: '/project/app-record-form',
    name: 'AppRecordForm',
    component: () => import('@/views/projectManage/AppRecordForm'),
    meta: {
      name: '项目工程进度记录表单-app',
      icon: ''
    },
    hidden: true
  }
]
/**
 * asyncRoutes
 * the routes that need to be dynamically loaded based on user roles
 */
export const asyncRoutes = [
  // 新模块
  projectRouter,
  equipmentRouter,
  // equipmentArchives,
  operationRouter,
  resource,
  setting
  // 旧页面路由模块
  // businessRouter,
  // engineeringRouter,
  // testRouter,
  // testRouter,
]
export const defaultRoutes = [
  // 404 page must be placed at the end !!!
  {
    path: '*',
    redirect: '/404',
    hidden: true
  }
]
const createRouter = () =>
  new Router({
    // mode: 'history', // require service support
    scrollBehavior: () => ({
      y: 0
    }),
    routes: constantRoutes
  })

const router = createRouter()

// 重新实例化一个新的路由，替换之前的路由
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher
}

export default router



