// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index.js'
import ElementUI from 'element-ui'
import moment from 'moment'
import * as echarts from 'echarts'
import 'echarts-liquidfill'
import 'echarts-gl'
import './../src/utils/china'
import './../src/utils/bmap'
import less from 'less'
import 'element-ui/lib/theme-chalk/index.css'
import '@/styles/index.less' // global css
import '@/assets/icons' // 引入svg图片
import Vuex from 'vuex'
import api from '@/api' // 引入api
import wsCache from '@/cache' // 本地存储
import './permission' // permission control

import TableList from '@/components/BaseTable/index' // Table组件引入
import BMainTopComponent from '@/components/BMainTopComponent/index.js' // BMainTopComponent组件引入
import GenerateSheet from '@/components/GenerateSheet/index.js' // GenerateSheet
// 自定义指令集合
import './utils/directive.js'

Vue.use(Vuex)
Vue.use(less)
Vue.use(ElementUI)
Vue.use(TableList)
Vue.use(BMainTopComponent)
Vue.use(GenerateSheet)

Vue.config.productionTip = false

Vue.prototype.$api = api
Vue.prototype.$wsCache = wsCache
Vue.prototype.$moment = moment
Vue.prototype.$echarts = echarts

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
  // render: h => h(App)
})



