<template>
  <div>
    <el-row
      :class="{
        [element.options.customClass]: element.options.customClass ? true : false
      }"
      type="flex"
      :gutter="element.options.gutter || 0"
      :justify="element.options.justify"
    >
      <!-- :align="element.options.align" -->
      <el-col v-for="(item, index) in element.columns" :key="index" :span="item.span || 0">
        <template v-for="col in item.list">
          <generate-col-item
            v-if="col.type == 'grid'"
            :key="col.key"
            :model.sync="dataModels"
            :element="col"
            @input-change="onInputChange"
            :edit="edit"
          >
            <!-- <template v-slot:[blank.name]="scope" v-for="blank in blanks">
              <slot :name="blank.name" :model="scope.model"></slot>
            </template> -->
          </generate-col-item>

          <generate-tab-item
            v-else-if="col.type == 'tabs'"
            :key="col.key"
            :model.sync="dataModels"
            :element="col"
            @input-change="onInputChange"
            :edit="edit"
          >
          </generate-tab-item>

          <generate-sheet-item
            v-else
            :key="col.key"
            :models.sync="dataModels"
            :widget="col"
            @input-change="onInputChange"
            :edit="edit"
          >
          </generate-sheet-item>
        </template>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import GenerateSheetItem from './GenerateSheetItem'
import GenerateTabItem from './GenerateTabItem'

export default {
  name: 'generate-col-item',
  components: {
    GenerateSheetItem,
    GenerateTabItem
  },
  props: ['element', 'model', 'edit'],
  data() {
    return {
      dataModels: this.model
    }
  },
  methods: {
    onInputChange(value, field) {
      this.$emit('input-change', value, field)
    }
  },
  watch: {
    model: {
      deep: true,
      handler(val) {
        this.dataModels = this.model
      }
    },
    dataModels: {
      deep: true,
      handler(val) {
        this.$emit('update:model', val)
      }
    }
  }
}
</script>



