import { newPost, get } from '../axios'
import { coordinateHandler } from '@/utils/common'
import request from '../request'

export default {
  name: 'equipment',
  /*************** 租赁机组 ***************/

  // 获取租赁机组列表（分页）
  GetGensetInfoManage: function (params, query) {
    return newPost(params, query, '/Lease/GetGensetProjectGensetInfoManage')
  },
  // 查询机组各状态对应的数量
  findGenSetStatusCount: function (params, query) {
    return newPost(params, query, '/GeneratorSet/FindGenSetStatusCountV1')
  },
  // 查询机组履历信息
  findGenSetInfoResumeByPage: function (params, query) {
    return newPost(params, query, '/GensetPush/GetGensetInfoResumeByPage')
  },
  // 上传履历
  AddGensetInfoResume: function (params, query) {
    return newPost(params, query, '/GensetPush/AddGensetInfoResume')
  },
  // 下载履历
  DoDownloadGensetInfoResumeFile: function (params) {
    return get(params, '/GensetPush/DoDownloadGensetInfoResumeFile', 'blob')
  },
  // 删除履历
  EnableOrDisableGensetInfoResume: function (params, query) {
    return newPost(params, query, '/GensetPush/EnableOrDisableGensetInfoResume')
  },

  // 查询机组关重件信息
  findGetGensetInfoBOMList: function (params, query) {
    return newPost(params, query, '/GensetPush/GetGensetInfoBOMList')
  },
  // 获取设备关重件添加编辑模型
  GetAllGensetInfoBOMFoUpdate: function (params, query) {
    return newPost(params, query, '/GensetPush/GetAllGensetInfoBOMFoUpdate')
  },
  // 添加编辑设备关重件
  AddGensetInfoBOMList: function (params, query) {
    return newPost(params, query, '/GensetPush/AddGensetInfoBOMList')
  },

  // 获取公司全部机组（分页）
  // FindGeneratorSetsV1: function (params, query) {
  //   return newPost(params, query, '/GeneratorSet/FindGeneratorSetsV1')
  // },

  // 添加公司租赁机组
  AddGensetProjectGensetInfo: function (params, query) {
    return newPost(params, query, '/Lease/AddGensetProjectGensetInfo')
  },

  // 编辑公司租赁机组
  EditGensetProjectGensetInfo: function (params, query) {
    return newPost(params, query, '/Lease/EditGensetProjectGensetInfo')
  },

  // 删除公司租赁机组
  DeleteGensetProjectGensetInfo: function (params, query) {
    return newPost(params, query, '/Lease/DeleteGensetProjectGensetInfo')
  },

  /*************** 租赁机组 End ***************/

  /*************** 机组监控 ***************/
  // 机组控制
  control: function (p, q) {
    return newPost(p, q, '/GeneratorSet/Control')
  },
  // 机组轨迹
  findAllGeneratroSetOrbitRecordV3: function (p, q) {
    return newPost(p, q, '/GeneratorSet/FindAllGeneratroSetOrbitRecordV3')
      .then((value) => {
        // 位置纠偏 GPS->BD-09 （谷歌GPS坐标转百度地图BD09坐标）
        const data = value.Data
        if (data) {
          const OrbitList = data.OrbitList
          const GensetMap = data.GensetMap
          OrbitList &&
            OrbitList.forEach((item) => {
              const nl = coordinateHandler.gps_bd_encrypt(item.Latitude, item.Longitude)
              item.Latitude = nl.Latitude
              item.Longitude = nl.Longitude
            })
          if (GensetMap) {
            const nl = coordinateHandler.gps_bd_encrypt(GensetMap.Latitude, GensetMap.Longitude)
            GensetMap.Longitude = nl.Longitude
            GensetMap.Latitude = nl.Latitude
          }
        }
        return value
      })
      .catch((reason) => {
        throw reason
      })
  },
  // to get
  /*************** 机组监控 End***************/

  /*************** 数据记录 ***************/
  // 操作记录
  getOperateRecords: function (p, q) {
    return newPost(p, q, '/GeneratorSet/GetOperateRecords')
  },
  // 运行记录
  findGeneratorStartStopRecords: function (p, q) {
    return newPost(p, q, '/GeneratorSet/FindGeneratorStartStopRecords')
  },
  // 报警历史
  findGeneratorSetHistoryAlarm: function (p, q) {
    return newPost(p, q, '/GeneratorSet/FindGeneratorSetHistoryAlarm')
  },
  // 上线记录
  getOnOfflineRecords: function (p, q) {
    return newPost(p, q, '/GeneratorSet/GetOnOfflineRecords')
  },
  // 发电统计
  getTotalGenerationEnergy: function (p, q) {
    return newPost(p, q, '/GeneratorSet/GetTotalGenerationEnergy')
  },
  // 曲线参数
  findGeneratorSetStatisitcsItems: function (p, q) {
    return newPost(p, q, '/GeneratorSet/FindGeneratorSetStatisitcsItems')
  },
  // 历史曲线
  findGeneratorSetHistoryDataV3: function (p, q) {
    return newPost(p, q, '/GeneratorSet/FindGeneratorSetHistoryDataV3')
  },

  // 0.1S曲线-获取设备0.1S曲线列表
  GeneratorSetRecordingList: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GeneratorSetRecordingList')
  },

  // 0.1S曲线-删除设备0.1S曲线列表
  DeleteGeneratorSetFullRecord: function (params) {
    return get(params, '/GeneratorSet/DeleteGeneratorSetFullRecord')
  },

  // 0.1S曲线-获取设备启动曲线项目
  GetTestReportProjects: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetTestReportProjects')
  },

  // 0.1S曲线-获取设备一键报修曲线项目
  GetButtonRepairProjects: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetButtonRepairProjects')
  },

  // 0.1S曲线-获取设备报警曲线项目
  GetReportProjects: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetReportProjects')
  },

  // 0.1S曲线-获取设备报警曲线项目默认选中项
  GetAlarmProjects: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetAlarmProjects')
  },

  // 0.1S曲线-获取设备0.1S曲线详情 -- 一键报修、启动曲线
  GetGenRecordCurves: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetGenRecordCurves')
  },

  // 0.1S曲线-获取设备0.1S曲线详情 -- 报警曲线
  GetAlarmCurveData: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetAlarmCurveData')
  },

  /*************** 数据记录 End ***************/

  /*************** 机组报告 ***************/

  // 运行报告-起动可靠性分析报告
  GetGeneratorSetRecordDailyList: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetGeneratorSetRecordDailyList')
  },

  // 功率时间矩阵-获取设备功率时间矩阵
  GetGeneratorSetPowerTimeList: function (params, query) {
    return newPost(params, query, '/GeneratorSet/GetGeneratorSetPowerTimeList')
  },

  // 11.2.运行报告-获取设备运行报告
  GetGensetRunReport: function (params, query) {
    return newPost(params, query, '/GensetPush/GetGensetRunReport')
  },

  // 运行报告-获取设备运行报警报告
  GetGensetAlarmReport: function (params, query) {
    return newPost(params, query, '/GensetPush/GetGensetAlarmReport')
  }

  /*************** 机组报告 End ***************/
}



