 * @Autor: linxu
 * @Date: 2022-03-17 15:13:26
 * @LastEditors: linxu
 * @LastEditTime: 2022-04-08 13:49:29
 * @Description: 
-->
<template>
  <div :class="{ ele: true, ele_title: widget.type == 'text' }">
    <generate-element-item
      v-if="widget.type != 'titleContent' && widget.type != 'text'"
      :widget="widget"
      v-model="dataModel"
      :models.sync="dataModels"
      :edit="edit"
    >
    </generate-element-item>
    <!-- 记录表标题 -->
    <div
      v-if="widget.type == 'titleContent'"
      class="sheet-title"
      :style="{
        'font-size': widget.options.fontSize + 'px',
        'text-align': widget.options.textAlign,
        'font-weight': widget.options.fontWeight,
        height: '46px',
        'line-height': '46px'
      }"
    >
      <span> {{ widget.name }}</span>
    </div>
    <!-- 小标题 -->
    <div
      v-if="widget.type == 'text'"
      :class="{
        'sheet-content__title': true,
        [widget.options.customClass]: widget.options.customClass ? true : false
      }"
      :style="{ ...widget.options.styleList }"
    >
      <span v-if="widget.htmlText" v-html="widget.htmlText || ''"></span>
      <span v-else>{{ widget.name }}</span>
    </div>
  </div>
</template>

<script>
import GenerateElementItem from './GenerateElementItem'

export default {
  components: {
    GenerateElementItem
  },
  props: ['widget', 'models', 'edit'],
  data() {
    return {
      dataModel: this.models[this.widget.model],
      dataModels: this.models
    }
  },
  created() {
    // 
  },
  methods: {},
  watch: {
    dataModel: {
      deep: true,
      handler(val) {
        // 
        this.models[this.widget.model] = val
        this.$emit('update:models', {
          ...this.models,
          [this.widget.model]: val
        })
        this.$emit('input-change', val, this.widget.model)
      }
    },
    models: {
      deep: true,
      handler(val) {
        // 
        this.dataModel = val[this.widget.model]
        this.dataModels = val
      }
    }
  }
}
</script>
<style lang="less" scoped>
.ele {
  /deep/.sheet-title {
    border: 1px solid #e4e4e4;
    border-top: 0;
    border-left: 0;
    // margin: 0 0 -1px -1px;
  }
  /deep/.sheet-content__title {
    padding: 4px;
    border: 1px solid #e4e4e4;
    border-top: 0;
    border-left: 0;
    line-height: 32px;
    min-height: 32px;
    height: 100%;
    span {
      line-height: 20px;
    }
  }
}
.ele_title {
  height: 100%;
}
</style>



