import Layout from '@/layout'

const resource = {
  path: '/resource',
  name: 'resource',
  component: Layout,
  meta: {
    title: '资源管理',
    icon: 'resourceMain'
  },
  children: [
    {
      path: 'services-providers-management',
      name: 'Services-providers-management',
      component: () => import('@/views/operation'),
      meta: {
        title: '服务商管理',
        icon: '',
        module: 'Project',
        request: {}
      }
    },
    {
      path: 'spare-parts-management',
      name: 'Spare-parts-management',
      component: () => import('@/views/operation'),
      meta: {
        title: '备件管理',
        icon: '',
        module: 'Project',
        request: {}
      }
    }
  ]
}

export default resource



