import router, { asyncRoutes, defaultRoutes } from '@/router'
// import NProgress from 'nprogress' // progress bar
// import 'nprogress/nprogress.css' // progress bar style
// import config from '@/config'
import wsCache from '@/cache'
import store from './store'
import { filterAsyncRoutes } from '@/utils/asyncRouter.js'

// NProgress.configure({ showSpinner: false }) // NProgress configuration

const whiteList = ['/login', '/project/app-record-form'] // 不重定向白名单
router.beforeEach(async (to, from, next) => {
  // NProgress.start()
  let hasLogin = !!wsCache.get('loginFlag')
  if (hasLogin) {
    // 跳过登录验证
    //   if (sessionStorage.getItem("userInfo")) {
    if (to.path === '/login') {
      next()
      // NProgress.done()
    } else {
      // next()//
      // 从store中获取用户角色 // 从store中获取用户角色
      const hasRoles = await store.getters.roles // wsCache.get('userInfo').roles && wsCache.get('userInfo').roles.length > 0
      if (hasRoles && hasRoles.length > 0) {
        next()
      } else {
        try {
          // 获取用户角色
          const roles = wsCache.get('userInfo').roles // await store.state.roles
          store.commit('SET_ROLES', roles)
          // if (roles) {
          const accessRoutes = await store.dispatch('GenerateRoutes')
          // 动态添加路由到router内
          router.addRoutes(accessRoutes)
          // } else {
          //   await store.dispatch('RenderNewMenu')
          // }
          next({ ...to, replace: true })
        } catch (error) {
          // 清除用户登录信息后，回跳到登录页去
          next(`/login?redirect=${to.path}`)
          // NProgress.done()
        }
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      // window.location.href = 'login.html' // ?redirect=${to.path}
      next(`/login?redirect=${to.path}`) // 否则全部重定向到登录页
      // NProgress.done()
    }
  }
})

router.afterEach((to) => {
  // setTitle(to)
  // NProgress.done() // 结束Progress
})

