<template>
  <div class="main-top">
    <span class="tip"></span>
    <span>{{ title || this.$route.meta.title }}</span>
    <el-button
      v-if="isShowBackBtn"
      class="back-btn"
      icon="el-icon-arrow-left"
      @click="handleBack()"
    >
      返回
    </el-button>
    <el-button
      v-if="isShowRefreshBtn"
      class="refresh-btn"
      icon="el-icon-refresh"
      @click="handleRefresh()"
      >刷新</el-button
    >
  </div>
</template>

<script>
export default {
  name: 'BMainTopComponent',
  props: {
    title: {
      type: String,
      default: ''
    },
    isShowRefreshBtn: {
      type: Boolean,
      default: true
    },
    isShowBackBtn: {
      type: Boolean,
      default: false
    },
    isHandleBackClick: {
      type: Boolean,
      default: false
    }
  },
  inject: ['reload'], // 注入App中注册的变量
  methods: {
    handleRefresh() {
      this.reload()
    },
    handleBack() {
      this.isHandleBackClick ? this.$emit('handleBackClick') : this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
.main-top {
  height: 50px;
  line-height: 50px;
  background-color: rgba(243, 243, 243, 1);
  border-bottom: 1px solid rgba(228, 228, 228, 1);
  padding: 0 16px;
  .tip {
    display: inline-block;
    width: 5px;
    height: 18px;
    background: rgba(26, 188, 156, 1);
  }
  span {
    vertical-align: middle;
  }
  .refresh-btn,
  .back-btn {
    background: transparent;
    border: 1px solid #e4e4e4;
    float: right;
    margin-top: 9px;
    margin-left: 8px;
  }
}
</style>



