const getters = {
  // app data
  sidebar: (state) => state.app.sidebar,
  size: (state) => state.app.size,
  device: (state) => state.app.device,
  theme: (state) => state.app.theme,
  // permission data
  roles: (state) => state.permission.roles,
  permission_routes: (state) => state.permission.routes,
  addRouters: (state) => state.permission.addRouters,
  // crew data
  selectCrew: (state) => state.crewData.selectCrew,
  searchFormSave: (state) => state.crewData.searchFormSave,
  isExpand: (state) => state.crewData.isExpand,

  GensetBrowseRecords: (state) => state.crewData.GensetBrowseRecords, //浏览记录
  // monitoring screen data
  screenDataConditions: (state) => state.screenData.screenDataConditions,
  monitoringScreenOperation: (state) => state.screenData.monitoringScreenOperation,
  // project
  currentProject: (state) => state.project.currentProject
}
export default getters



