import wsCache from '@/cache'
const dictData = {
  state: {
    selectCrew: JSON.parse(wsCache.get('selectCrew')) || {},
    selectCrewId: 0,
    selectCrewName: '',
    selectCrewStatus: '',
    searchFormSave: JSON.parse(wsCache.get('searchFormSave')) || null, // 筛选条件
    isExpand: false, // 是否展开
    GensetBrowseRecords: JSON.parse(wsCache.get('GensetBrowseRecords')) || []
  },
  mutations: {
    TOGGLE_SELECTCREW(state, data) {
      state['selectCrew'] = data
      wsCache.set('selectCrew', JSON.stringify(data))
    },
    TOGGLE_SELECTCREWID(state, data) {
      state['selectCrewId'] = data
    },
    TOGGLE_SELECTCREWNAME(state, data) {
      state['selectCrewName'] = data
    },
    TOGGLE_SELECTCREWSTATUS(state, data) {
      state['selectCrewStatus'] = data
    },

    TOGGLE_SEARCHFORMSAVE(state, data) {
      state['searchFormSave'] = data
      wsCache.set('searchFormSave', JSON.stringify(data))
    },

    TOGGLE_ISEXPAND(state, data) {
      state['isExpand'] = data
    },
    TOGGLE_GENSETBROWSERECORDS(state, data) {
      state['GensetBrowseRecords'] = data
      wsCache.set('GensetBrowseRecords', JSON.stringify(data))
    }
  },
  actions: {
    toggleSelectCrew({ commit }, selectCrew) {
      commit('TOGGLE_SELECTCREW', selectCrew)
      // const { selectCrewId, selectCrewName, selectCrewStatus } = selectCrew
      // commit('TOGGLE_SELECTCREWID', selectCrewId)
      // commit('TOGGLE_SELECTCREWNAME', selectCrewName)
      // commit('TOGGLE_SELECTCREWSTATUS', selectCrewStatus)
    },
    toggleSearchFormSave({ commit }, searchFormSave) {
      commit('TOGGLE_SEARCHFORMSAVE', searchFormSave)
    },
    toggleIsExpand({ commit }, isExpand) {
      commit('TOGGLE_ISEXPAND', isExpand)
    },
    toggleGensetBrowseRecords({ commit }, GensetBrowseRecords) {
      commit('TOGGLE_GENSETBROWSERECORDS', GensetBrowseRecords)
    }
  }
}

export default dictData



