<!--
 * @Autor: linxu
 * @Date: 2022-03-18 14:28:46
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 14:05:11
 * @Description: 
-->
<template>
  <div class="form-table" :class="{ 'is-disabled': disabled }">
    <el-table
      :data="tableData"
      border
      row-class-name="is-hover-row"
      :span-method="spanMethod"
      :show-header="widget.options.showHeader == undefined ? true : false"
    >
      <el-table-column
        label="序号"
        fixed
        :min-width="widget.options.indexWidth"
        v-if="widget.options.indexShow"
      >
        <template slot-scope="scope">
          <div class="is-hover-col">
            <div class="scope-action" v-if="widget.options.dynamicData && tableData.length > 0">
              <span @click="handleRemove(scope.$index)">删除</span>
            </div>
            <div class="scope-index">
              <span>{{ scope.$index + 1 }}</span>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column v-if="columns.length == 0"></el-table-column>
      <template v-else v-for="(column, index) in columns">
        <el-table-column
          :key="index"
          :prop="column.model"
          :label="column.name"
          :min-width="column.options.minWidth"
          :align="column.options.align"
        >
          <template slot-scope="scope">
            <!-- <div
              class="scope-action"
              v-if="widget.options.dynamicData && index == 0 && tableData.length > 1"
            >
              <span @click="handleRemove(scope.$index)">删除</span>
            </div> -->
            <GenerateElementItem
              :edit="!disabled"
              :is-table="true"
              :widget="column"
              v-model="scope.row[column.model]"
              :models.sync="tableData[scope.$index]"
            >
            </GenerateElementItem>
          </template>
        </el-table-column>
      </template>
    </el-table>
    <div
      class="add_btn_box"
      v-if="widget.options.dynamicData && !(!edit || widget.options.disabled)"
    >
      <el-button icon="el-icon-plus" type="text" @click="handleAddRow"> 添加 </el-button>
    </div>
  </div>
</template>

<script>
import { getSpanMethod } from '@/utils/el-table-span-method'
const funC = function ({ row, column, rowIndex, columnIndex }) {
  return { rowspan: 1, colspan: 1 }
}
export default {
  name: 'GenerateTable',
  components: {
    GenerateElementItem: () => import('./GenerateElementItem.vue')
  },
  props: ['columns', 'value', 'models', 'disabled', 'widget', 'edit'],
  data() {
    return {
      tableData: this.value,
      dataModels: this.models
    }
  },
  computed: {
    spanMethod: {
      get() {
        return (this.spanMethod = this.widget.options.spanMethodParams
          ? getSpanMethod(this.tableData, this.widget.options.spanMethodParams)
          : funC)
      },
      set(val) {
        return val
      }
    }
  },
  created() {},
  mounted() {
    //
    // var _this = this
    // setTimeout(function () {
    //   _this.handleAddRow(3)
    //   setTimeout(function () {
    //     _this.handleRemove(_this.tableData.length - 1)
    //   }, 600)
    // }, 500)
  },
  methods: {
    handleAddRow(val) {
      if (this.tableData.length < 500) {
        let item = {}
        // item.show = val ? true : false
        for (let i = 0; i < this.columns.length; i++) {
          item[this.columns[i].model] = this.columns[i].options.defaultValue
        }
        this.tableData.push(item)
      } else {
        this.$message1({
          message: '不能添加超过500行',
          type: 'warning'
        })
      }
    },
    handleRemove(index) {
      this.tableData.splice(index, 1)
    }
  },
  watch: {
    value(val) {
      this.tableData = val
    },
    tableData: {
      deep: true,
      handler(val) {
        this.$emit('input', val)
      }
    },
    dataModels: {
      deep: true,
      handler(val) {
        this.$emit('update:models', val)
      }
    },
    models: {
      deep: true,
      handler(val) {
        this.dataModels = val
      }
    }
  }
}
</script>
<style lang="less" scoped>
.form-table {
  position: relative;
  width: 100%;
  color: #606266;
  /deep/.el-table {
    border: 0;
    // width: calc(100% - 8px) !important;
    .el-table__empty-block {
      width: 100% !important;
    }
    .ele-item .ele-content {
      border: 0;
      padding: 0;
    }
    .el-table__empty-block {
      display: none;
    }

    .scope-action {
      display: none;
      // position: absolute;
      color: red;
      cursor: pointer;
      z-index: 1;
    }

    .scope-index {
      display: block;
    }

    .is-hover-row.hover-row {
      // &:hover {
      .scope-action {
        display: block;
      }

      .scope-index {
        display: none;
      }
      // }
    }
  }
  &.is-disabled {
    background-color: #f5f7fa;

    &::after {
      display: block;
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      cursor: no-drop;
    }
  }

  .add_btn_box {
    line-height: 32px;
    text-align: center;
    border: 1px solid #e4e4e4;
    border-top: 0;
    border-left: 0;
  }
}
</style>



