import { asyncRoutes, constantRoutes, defaultRoutes } from '@/router'
import { filterAsyncRoutes } from '@/utils/asyncRouter.js'
import wsCache from '@/cache'
// import wsCache from '@/cache'
// import path from 'path'

const permission = {
  state: {
    routes: [],
    addRouters: [],
    roles: [],
    hasOpenPermission: false // 是否开始路由权限
  },
  mutations: {
    SET_ROUTERS: (state, routes) => {
      state.addRouters = routes
      state.routes = constantRoutes.concat(routes)
    },
    SET_ROLES: (state, data) => {
      state.roles = data
    }
  },
  actions: {
    GenerateRoutes({ commit, state }) {
      return new Promise((resolve) => {
        // if (!state.hasOpenPermission) {
        // 获取用户角色
        const roles = wsCache.get('userInfo').roles
        // 通过用户角色，获取到角色路由表
        const accessRoutes = filterAsyncRoutes(asyncRoutes, roles)
        commit('SET_ROUTERS', [...accessRoutes, ...defaultRoutes])
        resolve(accessRoutes.concat(defaultRoutes))
        // } else {
        // }
      })
    },
    RenderNewMenu({ commit }) {
      return new Promise((resolve) => {
        const result = asyncRoutes.concat(defaultRoutes) // 使用本地静态路由
        // const result = renderMenuRoute(asyncRoutes) // 比对后台返回的菜单数据
        commit('SET_ROUTERS', result)
        resolve(result)
      })
    }
  }
}

export default permission



