<!--
 * @Autor: linxu
 * @Date: 2022-01-18 13:47:11
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-09 11:43:26
 * @Description: BaseTable 组件
-->
<template>
  <div class="table-form-container_content">
    <!-- table表格 -->
    <el-table
      size="small"
      :border="border"
      style="width: 100%"
      :data="dataSource"
      v-loading="loading"
      v-bind="options"
      v-on="tableEvents"
      ref="multipleTable"
      @selection-change="handleSelectionChange"
    >
      <!-- 复选框 -->
      <el-table-column
        type="selection"
        style="width: 55px"
        v-if="options && options.selection && (!options.isShow || options.isShow())"
      />
      <!-- 序号 -->
      <el-table-column
        width="60"
        align="center"
        type="index"
        v-if="options && options.index"
        :index="handleChangeIndex"
        :label="options && options.labelIndex"
      />

      <!-- 表格数据 -->
      <template v-for="(column, index) in columns">
        <el-table-column
          :key="index"
          v-bind="column.props"
          :prop="column.prop"
          :label="column.label"
          :align="column.align || 'center'"
          :width="column.width"
          :min-width="column.minWidth"
          v-if="!column.isShow || (column.isShow && column.isShow())"
        >
          <template slot-scope="scope">
            <template v-if="!column.render">
              <template v-if="column.formatter">
                <span
                  v-html="column.formatter(scope.row, column, scope.$index)"
                  @click="column.click && column.click(scope.row, scope.$index)"
                ></span>
              </template>
              <template v-else-if="column.newjump">
                <router-link
                  class="newjump"
                  type="primary"
                  :underline="false"
                  rel="opener"
                  v-bind="{ target: '_blank', ...column.target }"
                  :to="column.newjump(scope.row, column, scope.$index)"
                  >{{ scope.row[column.prop] || column.content }}</router-link
                >
              </template>
              <template v-else>
                <span
                  :style="column.click ? 'color: #409EFF; cursor: pointer;' : null"
                  @click="column.click && column.click(scope.row, scope.$index)"
                >
                  {{ scope.row[column.prop] || column.content }}
                  {{ `${scope.row[column.prop] && column.unit ? column.unit : ''}` }}
                </span>
              </template>
            </template>
            <template v-else>
              <render
                :column="column"
                :row="scope.row"
                :render="column.render"
                :index="index"
                @click="column.click && column.click(scope.row, scope.$index)"
              ></render>
            </template>
          </template>
        </el-table-column>
      </template>
      <!-- 机组配置情况 —— 插槽 -->
      <slot name="gensetConfig"></slot>

      <!-- 操作按钮 —— props传值 -->
      <el-table-column
        label="操作"
        align="center"
        v-bind="options && options.props"
        v-if="operates && operates.length > 0"
      >
        <template slot-scope="scope">
          <div class="operate-group">
            <template v-for="(btn, key) in operates">
              <span
                :key="key"
                v-if="!btn.isShow || (btn.isShow && btn.isShow(scope.row, scope.$index))"
              >
                <template v-if="btn.render">
                  <render
                    :column="scope.row"
                    :row="scope.row"
                    :render="btn.render"
                    :index="scope.$index"
                  ></render>
                </template>
                <template v-if="!btn.render">
                  <el-button
                    :size="btn.size || 'small'"
                    :type="btn.type || `text`"
                    :icon="btn.icon"
                    :plain="btn.plain"
                    :style="btn.setStyle && btn.setStyle(scope.row, scope.$index)"
                    v-bind="btn.props"
                    @click.native.prevent="btn.method(scope.row, scope.$index)"
                    :disabled="btn.disabled && btn.disabled(scope.row, scope.$index)"
                  >
                    {{ btn.label }}{{ operates.length >= 2 ? '&nbsp;&nbsp;' : '' }}
                  </el-button>
                </template>
              </span>
              <el-divider
                v-if="options.operateDivider && key < operates.length - 1"
                :key="key"
                direction="vertical"
              ></el-divider>
            </template>
          </div>
        </template>
      </el-table-column>

      <!-- 操作按钮 —— 插槽 -->
      <slot name="operation"></slot>
    </el-table>
    <!-- <br /> -->
    <!-- 分页部分 -->
    <el-pagination
      background
      class="pagination"
      v-if="pagination && dataTotal"
      :small="pagination.small"
      :total="dataTotal"
      :page-size="pagination.pageSize"
      :current-page="pagination.currentPage"
      @size-change="handleChangeSize"
      @current-change="handleChangePage"
      :page-sizes="pagination.pageSizes"
      :pager-count="pagination.pagerCount"
      :layout="pagination && pagination.layout ? pagination.layout : 'total, prev, pager, next'"
      v-on="options && options.handlePageEvent"
    />
  </div>
</template>

<script>
export default {
  name: 'TableList',
  props: {
    dataSource: {
      type: Array,
      default: () => {
        return []
      }
    },
    columns: {
      type: Array,
      default: () => {
        return []
      }
    },
    border: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    operates: {
      type: Array
    },
    pagination: {
      type: Object,
      default: null
    },
    dataTotal: {
      type: Number,
      default: 0
    },
    options: Object,
    tableEvents: Object,
    defaultSelectData: {
      //多选默认选中数据（回显）
      type: Array,
      default: () => []
    }
  },
  watch: {
    defaultSelectData: {
      handler(newVal, oldVal) {
        if (newVal && newVal.length > 0) {
          // console.log('默认选中', newVal, this.dataSource)
          newVal.forEach((selectItem) => {
            this.dataSource.forEach((item) => {
              let dataparams = []
              if (item.GeneratorSetId === selectItem.GeneratorSetId) {
                dataparams.push(item)
                this.toggleSelection(dataparams)
              }
            })
          })
        }
      },
      deep: true
    }
  },
  data() {
    return {
      formSearch: {},
      multipleSelection: []
    }
  },

  mounted() {},

  methods: {
    // 在获取tableData数据的接口中调用
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row, true)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },

    // 复选框选中项
    handleSelectionChange(val) {
      this.multipleSelection = val
      this.$emit('handleSelectionChange', Array.from(val))
    },
    // 序号递增
    handleChangeIndex(val) {
      if (this.pagination) {
        return (this.pagination.currentPage - 1) * this.pagination.pageSize + val + 1
      }
      return val + 1
    },
    // 改变分页触发事件
    handleChangePage(val) {
      this.$emit('handleChangePage', val)
    },
    // 改变分页触发事件
    handleChangeSize(val) {
      this.$emit('handleChangeSize', val)
    }
  },

  components: {
    render: {
      functional: true,
      props: {
        row: Object,
        render: Function,
        index: Number,
        column: {
          type: Object,
          default: null
        }
      },
      render: (h, opt) => {
        const params = {
          row: opt.props.row,
          index: opt.props.index
        }
        if (opt.props.column) params.column = opt.props.column
        return opt.props.render(h, params)
      }
    }
  }
}
</script>

<style lang="less">
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type='number'] {
  -moz-appearance: textfield;
}
</style>

<style lang="less" scoped>
.table-form-container_content {
  overflow: hidden;
  flex: 1;
  display: flex;
  flex-direction: column;
  // background: #fff;
  .btn-operates {
    margin-bottom: 6px;
    a {
      color: #fff;
      text-decoration: none;
      display: inline-block;
    }
  }
}
.table-header {
  padding-top: 10px;
  .table-header_button {
    text-align: right;
    float: right;
    margin-bottom: 12px;
    line-height: 40px;
  }
  .table-header-item .el-form-item {
    width: 100%;
    display: flex;
    flex: auto;
    margin-bottom: 12px;
    .el-form-item__content,
    .el-select {
      width: 100%;
    }
  }
}
.newjump {
  font-size: 12px;
  text-decoration: none;
  color: dodgerblue;
}

.pagination {
  text-align: right;
  padding: 16px 16px 8px;
  /deep/span {
    height: 32px;
    line-height: 32px;
  }
}
</style>



