import Layout from '@/layout'

const projectRouter = {
  path: '/project',
  name: 'project',
  component: Layout,
  meta: {
    title: '项目管理',
    icon: 'business',
    roles: ['普通用户', '服务管理']
  },
  children: [
    {
      path: 'project-list',
      name: 'project-list',
      component: () => import('@/views/projectManage'),
      meta: {
        title: '项目列表',
        icon: '',
        module: 'Project',
        request: {}
      }
    },
    {
      path: 'project-monitor-old',
      name: 'project-monitor-old',
      // component: () => import('@/views/home/projectMonitoring'),
      component: () => import('@/views/projectMonitoring/projectMonitor.vue'),
      meta: {
        title: '项目监控-旧',
        icon: '',
        module: 'Project',
        request: {}
      },
      hidden: true
    },
    {
      path: 'project-monitor',
      name: 'project-monitor',
      component: () => import('@/views/MouldPageProject'),
      // component: () => import('@/views/projectMonitoring/projectMonitorNew.vue'),
      meta: {
        title: '项目监控',
        icon: '',
        module: 'Project',
        request: {},
        activeNum: '3'
      }
    },
    {
      path: 'project-monitor-detail',
      name: 'project-monitor-detail',
      component: () => import('@/views/projectMonitoring/projectMonitoringDetail.vue'),
      meta: {
        title: '项目监控详情',
        icon: '',
        module: 'Project',
        request: {},
        activeMenu: '/project/project-monitor'
      },
      hidden: true
    },
    {
      path: 'project-detail',
      name: 'project-detail',
      component: () => import('@/views/MouldPageProject'),
      // component: () => import('@/views/projectManage/projectDetails'),
      meta: {
        title: '项目详情',
        icon: '',
        module: 'Project',
        request: {},
        activeMenu: '/project/project-list',
        activeNum: '1'
      },
      hidden: true
    },
    {
      path: 'controller-configuration',
      name: 'controller-configuration',
      // component: () => import('@/views/MouldPageGenset'),
      component: () => import('@/views/equipmentManage/gensetConfig/ControllerConfiguration'),
      meta: {
        title: '控制器配置',
        icon: '',
        module: 'Project',
        request: {},
        activeMenu: '/project/project-list',
        activeNum: '2'
      },
      hidden: true
    },
    {
      path: 'crew-manage',
      name: 'crew-manage',
      component: () => import('@/views/MouldPageProject'),
      // component: () => import('@/views/projectManage/projectProgressManage'),
      meta: {
        title: '工程管理',
        icon: '',
        module: 'Project',
        request: {},
        activeMenu: '/project/project-list',
        activeNum: '4'
      },
      hidden: true
    },
    {
      path: 'completion-record',
      name: 'completion-record',
      component: () => import('@/views/MouldPageProject'),
      // component: () => import('@/views/projectManage/projectCompletionRecord'),
      meta: {
        title: '完工记录',
        icon: '',
        module: 'Project',
        request: {},
        activeMenu: '/project/project-list',
        activeNum: '5'
      },
      hidden: true
    },
    {
      path: 'project-progress-record',
      name: 'project-progress-record',
      component: () => import('@/views/projectManage/projectProgressManage/ProjectProgressRecords'),
      meta: {
        title: '项目工程进度',
        icon: '',
        module: 'Project',
        request: {},
        activeMenu: '/project/project-list'
      },
      hidden: true
    },
    {
      path: 'genset-progress-manage',
      name: 'genset-progress-manage',
      component: () => import('@/views/projectManage/projectProgressManage/genset-progress'),
      meta: {
        title: '单机工程进度',
        icon: '',
        module: 'Project',
        request: {},
        activeMenu: '/project/project-list'
      },
      hidden: true
    }
  ]
}

export default projectRouter



