import { get, post, newPost } from '../axios'
import { coordinateHandler } from '@/utils/common'

export default {
  name: 'monitoringScreenOperation',
  // 大屏数据
  FindGensetProjectIndex: function (params, query) {
    return newPost(params, query, '/Lease/FindGensetProjectIndex')
  },
  // 机组架构
  FindGensetProjectIndexProjectTree: function (params, query) {
    return newPost(params, query, '/Lease/FindGensetProjectIndexProjectTree')
  },
  // 机组分布
  FindGensetProjectIndexMap: function (params, query) {
    return newPost(params, query, '/Lease/FindGensetProjectIndexMap')
      .then((value) => {
        const data = !!value && !!value.Data && value.Data
        if (data) {
          // 坐标转换 GPS->BD-09（谷歌GPS坐标转百度地图BD09坐标）
          data.forEach((item) => {
            const { Latitude, Longitude } = { ...item }
            const newCoordinate = coordinateHandler.gps_bd_encrypt(Latitude, Longitude)
            item.Latitude = newCoordinate.Latitude
            item.Longitude = newCoordinate.Longitude
          })
        } else {
        }
        return value
      })
      .catch((reason) => {
        throw reason
      })
  },
  // 机组分布（国家、省）
  findGensetProjectIndexMapCountryProvince: function (p, q) {
    return newPost(p, q, '/Lease/FindGensetProjectIndexMapCountryProvince')
  },
  // 客户反馈
  FindGensetProjectIndexFeedback: function (params, query) {
    return newPost(params, query, '/Lease/FindGensetProjectIndexFeedback')
  },
  // 反馈详情
  GetGensetProjectFeedback: function (params, query) {
    return newPost(params, query, '/Lease/GetGensetProjectFeedback')
  },
  /* 反馈回复
   * @param {Object} params {FeedbackId,ReplyDetail,FileUrl,Remark}
   * @param {Object} query {}
   * @returns {Promise}
   */
  AddGGensetProjectFeedbackReply: function (params, query) {
    return newPost(params, query, '/Lease/AddGensetProjectFeedbackReply', 'multipart/form-data')
  },
  // 维保提醒(已包含在大屏数据接口中)
  FindGensetProjectIndexMaintenance: function (params, query) {
    return newPost(params, query, '/Lease/FindGensetProjectIndexMaintenance')
  },
  // 实时报警(已包含在大屏数据接口中)
  FindGensetProjectIndexAlarm: function (params, query) {
    return newPost(params, query, '/Lease/FindGensetProjectIndexAlarm')
  }
}



