<template>
  <div class="img-content">
    <template v-if="fileList.length > 0">
      <div class="img-item" v-for="(item, index) in fileList" :key="index">
        <img
          :src="previewUrl(item.url)"
          class="avatar"
          :style="{ width: width + 'px', height: height + 'px' }"
        />
        <div
          class="mask-img"
          :style="{ lineHeight: height + 'px' }"
          @click="
            (event) => {
              event.stopPropagation()
            }
          "
        >
          <i
            class="el-icon-zoom-in avatar-uploader-icon"
            @click="handlePictureCardPreview(item, index)"
          ></i>
          <i
            class="el-icon-delete avatar-uploader-icon delete_btn"
            v-if="!disabled"
            @click="handleRemove(item, index)"
          ></i>
        </div>
      </div>
    </template>
    <el-upload
      :class="['avatar-uploader']"
      accept="image/jpeg,image/png,image/jpg,image/bmp,image/gif"
      action="#"
      :style="{ width: width, height: height }"
      :disabled="disabled"
      :limit="limit"
      multiple
      :before-upload="beforeAvatarUpload"
      :on-change="getValChange"
      :on-exceed="exceedLimit"
      :file-list="fileList"
      :auto-upload="false"
      :show-file-list="false"
    >
      <i
        v-show="!disabled"
        slot="default"
        class="el-icon-plus avatar-uploader-icon"
        :style="{ width: width + 'px', height: height + 'px', lineHeight: height + 'px' }"
      ></i>
      <div v-if="tip" class="el-upload__tip" slot="tip">{{ tip }}</div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible" title="图片预览" append-to-body class="previewDialog">
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    styleStr: {
      type: Object,
      default: () => {}
    },
    width: {
      type: Number,
      default: 178
    },
    height: {
      type: Number,
      default: 178
    },
    disabled: {
      type: Boolean,
      default: false
    },
    limit: {
      type: Number,
      default: 1
    },
    tip: {
      type: String,
      default: ''
    },
    value: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      hideUpload: false,
      fileList: this.value,
      dialogImageUrl: '',
      dialogVisible: false
    }
  },
  methods: {
    /*********** 文件上传 自动上传 start **************/

    // 上传触发前 对文件格式的校验
    beforeAvatarUpload(file) {},
    exceedLimit(files, fileList) {
      this.$message.error('上传文件个数不能超过' + this.limit + '个')
    },
    // 文件值改变时触发 change事件
    getValChange(file, fileList) {
      const type = file.raw.type
      let isCorrectType = ['image/jpeg', 'image/png', 'image/jpeg', 'image/gif'].includes(type)
      if (!isCorrectType) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.error('只能上传图片文件!')
        return false
      }
      const isLt5M = file.size / 1024 <= 5120
      if (!isLt5M) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件大小不能超过 5MB!')
        return false
      }

      this.getfileUpload(file, fileList)
    },
    // 上传调用后台接口
    getfileUpload(file, fileList) {
      const loading = this.$loading({
        lock: true,
        text: '处理中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      let formData = new FormData()
      formData.append('file', file.raw)

      this.$api.common.uploadFile(formData).then((res) => {
        if (res.Code == 1) {
          this.$message.success('上传成功!')
          const list = []
          res.Data.map((item) => {
            let fileObj = {
              url: item
            }
            list.push(fileObj)
          })
          this.$set(this, 'fileList', this.fileList.concat(list))
          this.$nextTick(() => {
            this.$emit('input', this.fileList)
          })
        } else {
          // this.$message.error(res.error);
          this.fileList = []
        }
        setTimeout(() => {
          loading.close()
        }, 300)
      })
    },
    previewUrl(url) {
      return `${process.env.BASE_API}/${url}`
    },
    handleRemove(file, index) {
      this.fileList.splice(index, 1)
    },
    handlePictureCardPreview(file, index) {
      this.dialogImageUrl = this.previewUrl(file.url)
      this.dialogVisible = true
    }
    /*********** 文件上传 end **************/
  },
  watch: {
    value(val) {
      this.fileList = val
    }
  }
}
</script>
<style lang="less">
.img-content {
  flex: 1;
}
.img-item {
  position: relative;
  margin: 8px 16px 8px 0;
  float: left;
  img {
    border-radius: 6px;
  }
  .mask-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    // height: 100%;
    display: none;
    text-align: center;
    .avatar-uploader-icon {
      width: 24px;
      height: 24px;
      color: white;
      cursor: pointer;
    }
    .avatar-uploader-icon.delete_btn {
      margin-left: 10px;
    }
  }
  &:hover .mask-img {
    background-color: rgba(0, 0, 0, 0.5);
    display: block;
  }
}

.avatar-uploader {
  min-height: 32px;
  .el-icon-plus {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    margin: 8px 0;
    box-sizing: border-box;
  }
  .el-upload:hover {
    border-color: #409eff;
  }
}
.avatar-uploader-icon {
  font-size: 24px;
  color: #8c939d;
  text-align: center;
}
</style>



