import { get, newPost } from '../axios'

export default {
  name: 'setting',
  // *** 横幅广告
  // 获取列表
  GetGensetProjectBannerManage: function (params, query) {
    return newPost(params, query, '/Lease/GetGensetProjectBannerManage')
  },
  // 添加广告
  AddGensetProjectBanner: function (p, q) {
    // return newPost(p, q, '/Lease/AddGensetProjectBanner','multipart/form-data')
    return newPost(p, q, '/Lease/AddGensetProjectBanner')
  },
  // 编辑广告
  EditGensetProjectBanner: function (p, q) {
    return newPost(p, q, '/Lease/EditGensetProjectBanner')
  },
  // 删除广告
  DeleteGensetProjectBanner: function (p, q) {
    return newPost(p, q, '/Lease/DeleteGensetProjectBanner')
  },
  // 发布广告（启用/禁用状态）
  PublishOrCancelGensetProjectBanner: function (p, q) {
    return newPost(p, q, '/Lease/PublishOrCancelGensetProjectBanner')
  }
}



