import wsCache from '@/cache'

const projectData = {
  state: {
    // currentProject: {}
    currentProject: JSON.parse(wsCache.get('currentProject')) || {}
  },
  mutations: {
    TOGGLE_CURRENTPROJECT(state, data) {
      state['currentProject'] = data
      wsCache.set('currentProject', JSON.stringify(data))
    }
  },
  actions: {
    toggleCurrentProject({ commit }, currentProject) {
      commit('TOGGLE_CURRENTPROJECT', currentProject)
    }
  }
}

export default projectData



