<template>
  <div id="app">
    <router-view v-if="isRouterAlive" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { addClass, removeClass } from '@/utils'

export default {
  name: 'App',
  provide() {
    // 父组件通过provide来提供变量，在子组件中通过inject来注入变量
    return {
      reload: this.reload
    }
  },
  computed: {
    ...mapGetters(['theme'])
  },
  data() {
    return {
      isRouterAlive: true // 控制视图是否显示
    }
  },
  methods: {
    // async getMenuList () {
    // const res = await this.$api.common.getMenuList()
    // if (res) {
    //   this.$wsCache.set('menuList', res)
    // }
    // },
    reload() {
      this.isRouterAlive = false
      this.$nextTick(() => {
        this.isRouterAlive = true
      })
    }
  },
  mounted() {
    // init theme style
    const theme = this.theme
    const body = document.body
    if (theme === 'dark') {
      removeClass(body, 'theme-light')
      addClass(body, 'theme-dark')
    } else {
      removeClass(body, 'theme-dark')
      addClass(body, 'theme-light')
    }
    //
  }
}
</script>
<style lang="less">
</style>



