<template>
  <div class="navbar">
    <!-- <svg-icon icon-class='home' /> -->
    <el-image class="nav-logo" @click="changeTheme" :src="logoUrl"></el-image>
    <div class="right-menu">
      <el-dropdown class="avatar-container right-menu-item hover-effect" trigger="hover">
        <div class="avatar-wrapper">
          <svg-icon style="font-size: 30px; margin-top: 5px" icon-class="Icon-account"></svg-icon>
          <span>{{ this.$wsCache.get('userInfo').displayName }}</span>
          <!-- <i class="el-icon-caret-bottom" /> -->
        </div>
        <el-dropdown-menu class="user-drop-menu" slot="dropdown">
          <div class="user-info-box">
            <p>
              账户信息<el-button
                class="account-setting-btn"
                type="text"
                @click="handleAccountSetting"
                >账户设置</el-button
              >
            </p>
            <ul>
              <li><span>所在部门:</span><em></em></li>
              <li><span>本次登录:</span><em></em></li>
              <li><span>登陆地区:</span><em></em></li>
              <li><span>上次登录:</span><em></em></li>
            </ul>
          </div>
          <!-- <el-dropdown-item divided
                            @click.native="logout">
            <span style="display:block;">退出</span>
          </el-dropdown-item> -->
        </el-dropdown-menu>
      </el-dropdown>
      <el-divider direction="vertical"></el-divider>
      <!-- 跳转到运维大屏首页 -->
      <svg-icon
        style="font-size: 17px"
        icon-class="Icon-data"
        className="btn"
        title="返回首页"
        @click="jump('/ms')"
      ></svg-icon>
      <el-divider direction="vertical"></el-divider>
      <!-- <svg-icon
        icon-class="changeTheme"
        className="btn"
        title="切换主题（测试功能）"
        @click="changeTheme"
      ></svg-icon>
      <el-divider direction="vertical"></el-divider> -->
      <!-- <svg-icon
        style="font-size: 16px"
        icon-class="Icon-home"
        className="btn"
        title="返回工作台"
        @click="jump('/syshomePage/home')"
      ></svg-icon> -->
      <!-- <el-divider direction="vertical"></el-divider> -->
      <svg-icon
        style="font-size: 15px; padding: 2px 0 0 0"
        icon-class="Icon-logout"
        className="btn"
        title="退出"
        @click="logout"
      ></svg-icon>
    </div>

    <account-setting
      v-if="showAccountSettingDialog"
      @hiddenAccountSettingDialog="hiddenAccountSettingDialog"
    ></account-setting>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

import AccountSetting from './AccountSetting'
import { addClass, removeClass } from '@/utils'
export default {
  components: {
    AccountSetting
  },
  data() {
    return {
      logoUrl: require('@/assets/img/logo.png'),
      showAccountSettingDialog: false
    }
  },
  computed: {
    ...mapGetters(['theme'])
  },
  methods: {
    ...mapActions(['setTheme', 'toggleSelectCrew']),
    async logout() {
      const res = await this.$api.login.logout()
      if (res.IsSuccess) {
        // this.$wsCache.delete('loginFlag')
        // this.$wsCache.delete('userInfo')
        // this.$wsCache.delete('selectCrew')
        this.$wsCache.clear()
        this.toggleSelectCrew('')
        this.$router.push('/login')
        // window.location.reload()
      }
    },
    jump(url) {
      this.$router.push(url)
    },
    handleAccountSetting() {
      this.showAccountSettingDialog = true
    },
    hiddenAccountSettingDialog() {
      this.showAccountSettingDialog = false
    },
    changeTheme() {
      const body = document.body
      if (body.className.includes('theme-dark')) {
        removeClass(body, 'theme-dark')
        addClass(body, 'theme-light')
      } else {
        removeClass(body, 'theme-light')
        addClass(body, 'theme-dark')
      }

      this.setTheme(this.theme === 'dark' ? 'light' : 'dark')
    }
  }
}
</script>

<style lang="less" scoped>
.navbar {
  height: 50px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  background: rgba(10, 22, 46, 1); //rgba(26, 188, 156, 0.9);
  .nav-logo {
    width: 195px;
    height: 50px;
    margin-left: 24px;
  }

  .breadcrumb-container {
    float: left;
  }

  .errLog-container {
    display: inline-block;
    vertical-align: top;
  }

  .right-menu {
    float: right;
    height: 100%;
    line-height: 50px;
    margin-right: 24px;
    display: flex;
    align-items: center;

    &:focus {
      outline: none;
    }

    .avatar-container {
      .avatar-wrapper {
        position: relative;
        height: 40px;
        line-height: 40px;

        .user-avatar {
          cursor: pointer;
          width: 40px;
          height: 40px;
          border-radius: 10px;
          vertical-align: top;
        }
        > span {
          margin-left: 8px;
          vertical-align: top;
          color: #fff;
        }
      }
    }

    .el-divider {
      background: #e4e4e4;
    }
  }
}
.user-drop-menu {
  padding: 0;
  margin: 0;
  .user-info-box {
    margin: 0 8px 16px;
    width: 420px;
    box-sizing: border-box;
    color: #999999;
    p {
      padding: 0 16px;
      line-height: 40px;
      position: relative;
      border-bottom: 1px solid #e4e4e4;
      .account-setting-btn {
        position: absolute;
        right: 16px;
        top: 4px;
      }
    }
    ul {
      line-height: 24px;
      margin-top: 16px;
      list-style: disc;
      padding: 0 24px;
      li {
        &::marker {
          float: left;
        }
      }
    }
  }
}
</style>



