import request from './request'

function get(params, url, responseType) {
  // return request({
  //   url: url,
  //   method: 'get',
  //   params,
  //   headers: {
  //     'Content-Type': headersType || 'application/json;charset=UTF-8'
  //   },
  //   responseType: responseType
  // })
  return new Promise(function (resolve, reject) {
    request({
      url: url,
      method: 'get',
      params,
      responseType: responseType
    })
      .then((res) => resolve(res))
      .catch((err) => reject(err))
  })
}

function post(params, url, headersType) {
  // return request({
  //   url: url,
  //   method: 'post',
  //   data: params,
  //   headers: {
  //     'Content-Type': headersType || 'application/json;charset=UTF-8'
  //   }
  // })
  return new Promise(function (resolve, reject) {
    request({
      url: url,
      method: 'post',
      data: params,
      headers: {
        'Content-Type': headersType || 'application/json;charset=UTF-8'
      }
    })
      .then((res) => {
        resolve(res)
      })
      .catch((err) => reject(err))
  })
}

// 带url参数的post请求
function newPost(params, query, url, headersType) {
  return request({
    url: url,
    method: 'post',
    data: params,
    params: query,
    headers: {
      'Content-Type': headersType || 'application/json;charset=UTF-8'
    }
  })
}

function put(params, url, headersType) {
  return request({
    url: url,
    method: 'put',
    data: params,
    headers: {
      'Content-Type': headersType || 'application/json;charset=UTF-8' // 'application/x-www-form-urlencoded'
    }
  })
}

function del(params, url) {
  return request({
    url: url,
    method: 'delete',
    params
  })
}

export { get, post, newPost, put, del }



