import axios from 'axios'
// import qs from 'qs'
// import config from '@/config'
// import wsCache from '@/cache'
import router from '../router'
import { Message } from 'element-ui'

const PATH_URL = process.env.NODE_ENV === 'development' ? '/test' : process.env.BASE_API

// 创建axios实例
const service = axios.create({
  baseURL: PATH_URL, // process.env.BASE_API, // api 的 base_url
  timeout: 30000, // 请求超时时间
  withCredentials: true
})
// request拦截器
service.interceptors.request.use(
  (config) => {
    // console.log(config)
    // if (wsCache.get('userInfo')) {
    //   config.headers['token'] = wsCache.get('userInfo').token
    // }
    if (
      config.method === 'post' &&
      config.headers['Content-Type'] === 'application/x-www-form-urlencoded'
    ) {
      config.data = JSON.stringify(config.data)
    }
    // if (config.method === 'put' && config.headers['Content-Type'] === 'application/x-www-form-urlencoded') {
    //   config.data = qs.stringify(config.data)
    // }
    return config
  },
  (error) => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// response 拦截器
service.interceptors.response.use(
  (response) => {
    if (response.status && response.status === 200) {
      const res = response.data
      if (res.Code !== 1) {
        if (res.Code === undefined) {
          return Promise.resolve(response)
        } else if (res.Code === 207) {
          router.push({ path: '/login' })
        } else {
          if (response.type === 0) {
            Message({
              message: res.Message,
              type: 'warning'
            })
          } else {
            Message({
              message: res.Message,
              type: 'error'
            })
          }
        }
      }
      return Promise.resolve(res)
    } else {
      return Promise.reject(response)
    }
  },
  // 服务器状态码不是200的情况
  (error) => {
    if (error.response.status) {
      switch (error.response.status) {
        // 401: 未登录
        // 未登录则跳转登录页面，并携带当前页面的路径
        // 在登录成功后返回当前页面，这一步需要在登录页操作。
        case 401:
          router.replace({
            path: '/login',
            query: { redirect: router.currentRoute.fullPath }
          })
          break
        // 403 token过期
        // 登录过期对用户进行提示
        // 清除本地token和清空vuex中token对象
        // 跳转登录页面
        case 403:
          Message({
            message: '登录过期，请重新登录',
            duration: 1000,
            forbidClick: true
          })
          // 清除token
          // localStorage.removeItem('token')
          // store.commit('loginSuccess', null)
          // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
          setTimeout(() => {
            router.replace({
              path: '/login',
              query: {
                redirect: router.currentRoute.fullPath
              }
            })
          }, 1000)
          break
        // 404请求不存在
        case 404:
          Message({
            message: '网络请求不存在',
            duration: 1500,
            forbidClick: true
          })
          break
        // 其他错误，直接抛出错误提示
        default:
          Message({
            message: error.response.data.message,
            duration: 1500,
            forbidClick: true
          })
      }
      // console.log('err' + error) // for debug
      // Message({
      //   message: '请求超时或网络异常！',
      //   type: 'warning'
      // })
      return Promise.reject(error)
    }
  }
)

export default service



