import { get, post, newPost } from '../axios'
import request from '../request'

export default {
  // 路由获取
  getMenuList: function (params) {
    return get(params, '/commom/navigation')
  },

  // 机组list查询
  getCrewList: function (params, query) {
    return newPost(params, query, '/GeneratorSet/FindGeneratorSetsV1')
  },

  /**
   * @description 根据机组名称（模糊）和机组状态搜索机组位置信息
   * @param {Object} data {
    "StatusMode": "0",
    "MoreName": "0013"
    }
   * @returns {Promise}
   */
  findAllGeneratorSetsForMap: function (data) {
    // return post(data, `/GeneratorSet/FindAllGeneratorSetsForMap`)FindAllLeaseGeneratorSetsForMap
    return post(data, `/Lease/FindAllLeaseGeneratorSetsForMap`)
  },

  /**
   * @description 根据机组名称（模糊）和公司名称搜索公司-机组架构图
   * @param {Object} data {
    "model": {
        "Customer": "",
        "MoreName": "",
        "IsShowAll": true,
        "IsShowSelf": true
      }
    }
   * @returns {Promise}
   */ getCustomersSimpleGensetV2Tree: function (data) {
    return post(data, `Organization/GetCustomersSimpleGensetV2Tree`)
  },

  // 文件上传
  uploadFile: function (params) {
    return post(params, `/Lease/UploadGensetProjectFile`)
  }
}



