import { get, post } from '../axios'

export default {
  login: function (params) {
    return post(params, '/Account/Login')
  },
  logout: function (params) {
    return post(params, '/Account/Logout')
  },
  getUserInfo: function (params) {
    return get(params, '/Page/Share')
  },
  GetAllGensetCollectorVersionVCors: function (params) {
    return get(params, '/DownLoad/GetAllGensetCollectorVersionVCors')
  },
  GetTest: function (params) {
    return get(params, '/GensetPush/GetTest')
  }
}



