<template>
  <div>
    <!-- 左侧菜单栏
    <logo v-if="showLogo"
          :collapse="isCollapse" /> -->
    <el-scrollbar wrap-class="scrollbar-wrapper">
      <el-menu
        :default-active="activeMenu"
        :collapse="isCollapse"
        :background-color="variables.menuBg"
        :text-color="variables.menuTextColor"
        :unique-opened="false"
        :active-text-color="variables.menuActiveText"
        :collapse-transition="false"
        mode="vertical"
      >
        <sidebar-item
          v-for="route in newroutes"
          :key="route.path"
          :item="route"
          :base-path="route.path"
        />
        <!-- :base-path="route.path"  -->
      </el-menu>
    </el-scrollbar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Logo from './Logo'
import SidebarItem from './SidebarItem'
import variables from '@/styles/variables.less'

export default {
  data() {
    return {
      // newroutes: []
    }
  },
  components: { SidebarItem, Logo },
  computed: {
    ...mapGetters(['permission_routes', 'sidebar']),
    activeMenu() {
      const route = this.$route
      const { meta, path } = route
      // if set path, the sidebar will highlight the path you set
      if (meta.activeMenu) {
        return meta.activeMenu
      }
      return path
    },
    showLogo() {
      return this.$config.sidebarLogo
    },
    variables() {
      return variables
    },
    isCollapse() {
      return !this.sidebar.opened
    },
    newroutes() {
      return (
        this.permission_routes &&
        this.permission_routes.filter((item) => {
          if (!item.hidden) {
            return item
          }
        })
      )
    }
  },
  mounted() {
    // console.log('this.permission_routes', this.permission_routes, this.newroutes)
    // this.newroutes = this.permission_routes && this.permission_routes.map(item => {
    //   if (!item.hidden) {
    //     return item
    //   }
    // })
  }
}
</script>



