 * @LastEditTime: 2022-03-31 11:04:06
 * @Description: 
-->
<template>
  <div ref="generateSheet" >
      <template v-for="item in data.list">
        <generate-col-item
          v-if="item.type == 'grid'"
          :key="item.key"
          :model.sync="models"
          :element="item"
          @input-change="onInputChange"
          :edit="edit"
        >
        </generate-col-item>

        <generate-tab-item
          v-else-if="item.type == 'tabs'"
          :key="item.key"
          :model.sync="models"
          :element="item"
          @input-change="onInputChange"
          :edit="edit"
        >
        </generate-tab-item>

        <generate-sheet-item
          v-else
          :key="item.key"
          :models.sync="models"
          :widget="item"
          @input-change="onInputChange"
          :edit="edit"
        >
        </generate-sheet-item>
      </template>
    </el-form>
  </div>
</template>

<script>
import GenerateSheetItem from './GenerateSheetItem'
import GenerateColItem from './GenerateColItem'
import GenerateTabItem from './GenerateTabItem'
import _ from 'lodash'

export default {
  name: 'fm-generate-sheet',
  components: {
    GenerateSheetItem,
    GenerateColItem,
    GenerateTabItem
  },
  props: {
    data: {
      type: Object,
      default: () => []
    },
    value: {
      type: Object,
      default: () => ({})
    },
    edit: {
      type: Boolean,
      default: true
    }
    // 特殊控件远程数据源
    // remote: {
    //   type: Object,
    //   default: () => ({})
    // },
  },
  data() {
    return {
      models: {},
    }
  },
  created() {
    // 
    // 
    //  
    this.generateModel(this.data.list)
  },
  mounted() {
  },
  methods: {
    generateModel(genList) {
      for (let i = 0; i < genList.length; i++) {
        if(genList[i].type === 'titleContent' || genList[i].type === 'text'){
        } else if (genList[i].type === 'grid') {
          genList[i].columns.forEach((item) => {
            this.generateModel(item.list)
          })
        } else if (genList[i].type === 'tabs') {
          genList[i].tabs.forEach((item) => {
            this.generateModel(item.list)
          })
        } else {
          // 
          // 
          if (Object.keys(this.value).indexOf(genList[i].model) >= 0) {
            // 处理老版本没有dataBind值的情况，默认绑定数据
            if (
              Object.keys(genList[i].options).indexOf('dataBind') < 0 ||
              genList[i].options.dataBind
            ) {
              this.models[genList[i].model] =  _.cloneDeep(this.value[genList[i].model])
            }
          } else {
              if (
                Object.keys(genList[i].options).indexOf('dataBind') < 0 ||
                genList[i].options.dataBind
              ) {
                this.models[genList[i].model] =  _.cloneDeep(genList[i].options.defaultValue)
              }
          }
        }
      }
      // 
    },
    getData() {
      // 提交 必填项校验
        return new Promise((resolve, reject) => {
          const resData = {}
          Object.keys(this.models).forEach((key) => {
              resData[key] = this.models[key]
          })
          resolve(JSON.parse(JSON.stringify(resData)))
        })
    },
    onInputChange(value, field) {
      // 
      this.$emit('form-change', field, value, this.models)
    },
  },
  watch: {
    value: {
      deep: true,
      handler(val) {
        // 
        const models = { ...this.models, ...val }
        this.$set(this,'models',models)
      }
    }
  }
}
</script>

<style lang="less"></style>



