// 存在组件间循环引用的场景，因此在外部统一注册
import BMainTopComponent from './MainTop'

const install = (Vue) => {
  Vue.component(BMainTopComponent.name, BMainTopComponent)
}

export default {
  install,
  version: '1.0.0'
}



