import wsCache from '@/cache'

const screenData = {
  state: {
    screenDataConditions: JSON.parse(wsCache.get('screenDataConditions')) || {},
    // ** 运维监控大屏 {year,rentingState,operationState}
    monitoringScreenOperation: JSON.parse(wsCache.get('monitoringScreenOperation')) || {}
  },
  mutations: {
    TOGGLE_SCREENDATACONDITONS(state, data) {
      state['screenDataConditions'] = data
      wsCache.set('screenDataConditions', JSON.stringify(data))
    },
    TOGGLE_MONITORINGSCREENOPERATION(state, data) {
      state['monitoringScreenOperation'] = data
      wsCache.set('monitoringScreenOperation', JSON.stringify(data))
    }
  },
  actions: {
    toggleScreenDataConditons({ commit }, screenDataConditions) {
      commit('TOGGLE_SCREENDATACONDITONS', screenDataConditions)
    },
    toggleMonitoringScreenOperation({ commit }, monitoringScreenOperation) {
      commit('TOGGLE_MONITORINGSCREENOPERATION', monitoringScreenOperation)
    }
  }
}

export default screenData



