<template>
  <el-row
    v-if="widget.type != 'table'"
    :class="{
      'ele-item': true,
      [widget.options.customClass]: widget.options.customClass ? true : false
    }"
    style="position: relative"
  >
    <el-col
      class="ele-label"
      v-if="!widget.options.hiddenLabel"
      :span="widget.options.span ? widget.options.span[0] : widget.options.hiddenLabel ? 0 : 12"
    >
      <span>{{ widget.name }}</span>
    </el-col>
    <el-col
      class="ele-content"
      :style="{
        'text-align': widget.options.textAlign
      }"
      :span="widget.options.span ? widget.options.span[1] : widget.options.hiddenLabel ? 24 : 12"
    >
      <template v-if="widget.type == 'input'">
        <el-input
          v-if="
            widget.options.dataType == 'number' ||
            widget.options.dataType == 'integer' ||
            widget.options.dataType == 'float'
          "
          :type="widget.options.dataType"
          v-model.number="dataModel"
          :disabled="!edit || widget.options.disabled"
          :placeholder="widget.options.placeholder"
          :show-password="widget.options.showPassword"
          :style="{ width: widget.options.width }"
          @change="inputChange()"
          maxlength="150"
        ></el-input>
        <el-input
          v-else
          :type="widget.options.dataType"
          v-model="dataModel"
          :disabled="!edit || widget.options.disabled"
          :placeholder="widget.options.placeholder"
          :show-password="widget.options.showPassword"
          :style="{ width: widget.options.width }"
          @change="inputChange()"
          maxlength="150"
        ></el-input>
      </template>

      <template v-if="widget.type == 'textarea'">
        <el-input
          type="textarea"
          :rows="widget.options.limitRows || 3"
          v-model="dataModel"
          autosize
          :autosize="{ minRows: widget.options.limitRows || 2, maxRows: 6 }"
          :disabled="!edit || widget.options.disabled"
          :placeholder="widget.options.placeholder"
          :style="{ width: widget.options.width }"
          maxlength="500"
          @change="textareaChange()"
        ></el-input>
      </template>

      <template v-if="widget.type == 'radio'">
        <el-radio-group
          v-model="dataModel"
          :style="{ width: widget.options.width }"
          :disabled="!edit || widget.options.disabled"
          @change="radioChange()"
        >
          <el-radio
            :style="{
              display: widget.options.inline ? 'inline-block' : 'block',
              'margin-bottom': '10px'
            }"
            :label="item.value"
            v-for="(item, index) in widget.options.remote
              ? widget.options.remoteOptions
              : widget.options.options"
            :key="index"
          >
            <template v-if="widget.options.remote">{{ item.label }}</template>
            <template v-else>{{ widget.options.showLabel ? item.label : item.value }}</template>
          </el-radio>
        </el-radio-group>
      </template>

      <template v-if="widget.type == 'checkbox'">
        <el-checkbox-group
          v-model="dataModel"
          :style="{ width: widget.options.width }"
          :disabled="!edit || widget.options.disabled"
          @change="checkboxChange()"
        >
          <el-checkbox
            v-for="(item, index) in widget.options.options"
            :key="index"
            :style="{ display: widget.options.inline ? 'inline-block' : 'block' }"
            :label="item[widget.options.props.value]"
            >{{ item[widget.options.props.label] }}</el-checkbox
          >
        </el-checkbox-group>
      </template>

      <template v-if="widget.type == 'date'">
        <el-date-picker
          v-model="dataModel"
          range-separator="-"
          :type="widget.options.type"
          :placeholder="widget.options.placeholder"
          :start-placeholder="widget.options.startPlaceholder"
          :end-placeholder="widget.options.endPlaceholder"
          :readonly="widget.options.readonly"
          :disabled="!edit || widget.options.disabled"
          :editable="widget.options.editable"
          :clearable="widget.options.clearable"
          :value-format="widget.options.timestamp ? 'timestamp' : widget.options.format"
          :format="widget.options.format"
          :style="{ width: widget.options.width }"
          v-if="widget.options.laiyuan == 1"
          @change="dateChange()"
          placement="bottom-start"
        ></el-date-picker>
        <div v-else>{{ dataModel }}</div>
      </template>

      <template v-if="widget.type == 'select'">
        <el-select
          v-model="dataModel"
          :disabled="!edit || widget.options.disabled"
          :multiple="widget.options.multiple"
          :clearable="widget.options.clearable"
          :placeholder="widget.options.placeholder"
          :style="{ width: widget.options.width }"
          :filterable="widget.options.filterable"
          @change="selectChange()"
        >
          <el-option :value="0" :label="edit ? '请选择' : ' '"></el-option>
          <el-option
            v-for="(item, index) in widget.options.remote
              ? widget.options.remoteOptions
              : widget.options.options"
            :key="index"
            :value="item.value"
            :label="widget.options.showLabel || widget.options.remote ? item.label : item.value"
          ></el-option>
        </el-select>
      </template>

      <template v-if="widget.type == 'imgupload'">
        <fm-image-upload
          v-model="dataModel"
          :disabled="!edit || widget.options.disabled"
          :styleStr="widget.options.styleStr"
          :width="widget.options.size.width"
          :height="widget.options.size.height"
          :limit="widget.options.limit"
          :is-delete="widget.options.isDelete"
          :is-edit="widget.options.isEdit"
          :action="widget.options.action"
        ></fm-image-upload>
      </template>

      <!-- <template v-if="widget.type == 'fileupload'">
        <fm-file-upload
          v-model="dataModel"
          :action="widget.options.action"
          :width="isTable ? '100%' : widget.options.width"
          :disabled="!edit || widget.options.disabled"
          :limit="widget.options.limit"
          :tip="widget.options.tip"
        ></fm-file-upload>
      </template> -->
    </el-col>
  </el-row>

  <generate-table
    v-else-if="widget.type == 'table'"
    v-model="dataModel"
    :columns="widget.tableColumns"
    :models="dataModels"
    :disabled="!edit || widget.options.disabled"
    :widget="widget"
    :edit="edit"
  ></generate-table>
</template>

<script>
import GenerateTable from './GenerateTableItem'
import FmImageUpload from '@/components/Upload/image'
// import FmFileUpload from '@/components/Upload/file'

export default {
  components: {
    GenerateTable,
    FmImageUpload
    // FmFileUpload
  },
  props: ['widget', 'value', 'models', 'edit'],
  data() {
    return {
      dataModel: this.value,
      dataModels: { ...this.models }
    }
  },
  created() {
    // 
  },
  watch: {
    value(val) {
      this.dataModel = val
    },
    dataModel(val) {
      this.$emit('input', val)
    },
    models: {
      deep: true,
      handler(val) {
        this.dataModels = this.models
      }
    },
    dataModels: {
      deep: true,
      handler(val) {
        this.dataModel = val[this.widget.model]
        this.$emit('update:models', val)
      }
    }
  },
  methods: {
    inputChange() {
      
    },
    textareaChange() {
      
    },
    radioChange() {
      
    },
    checkboxChange() {
      
      // this.dataModel = (this.checkboxValue&&this.checkboxValue.length>0) ? this.checkboxValue.map(item =>{
      //   return {
      //     value: item
      //   }
      // }) : null
    },
    dateChange() {
      
    },
    selectChange() {
      // 
      // if (this.widget.options.multiple){
      //   this.dataModel = this.selectValue.map(item =>{
      //     return {
      //       value: item
      //     }
      //   })
      // } else {
      //   this.dataModel = this.selectValue
      // }

      
    }
  }
}
</script>
<style lang="less" scoped>
.ele-item {
  display: flex;
  // margin: 1px 1px 0 0;
  .ele-label {
    padding: 4px;
    box-sizing: border-box;
    border: 1px solid #e4e4e4;
    border-top: 0;
    border-left: 0;
    // margin: 0 0 -1px -1px;
    display: flex;
    align-items: center;
    span {
      flex: 1;
    }
  }

  /deep/.ele-content {
    padding: 4px;
    box-sizing: border-box;
    border: 1px solid #e4e4e4;
    border-top: 0;
    border-left: 0;
    // margin: 0 0 -1px -1px;
    .el-input__inner,
    .el-textarea__inner {
      border: 0;
      font-family: none;
    }

    .el-textarea__inner {
      resize: none;
    }

    .el-radio-group {
      .el-radio {
        line-height: 32px;
        margin-bottom: 0 !important;
        margin-right: 14px;
      }
    }

    .el-checkbox-group {
      .el-checkbox {
        line-height: 32px;
      }
    }

    .el-input.is-disabled .el-input__inner,
    .el-textarea.is-disabled .el-textarea__inner {
      background-color: #fff;
      color: #606266;
    }

    .el-select {
      text-align: center;
      width: 100%;
      .el-input__suffix {
        opacity: 0;
      }
    }
  }
}
// label content样式设置
.ele-item.labelCenter .ele-label,
.ele-item.textCenter /deep/.el-input__inner,
.ele-item.textCenter /deep/.el-radio-group,
.ele-item.textCenter /deep/.el-checkbox-group,
.ele-item.textCenter /deep/.el-select__tags {
  text-align: center;
  padding: 0;
}

// table 内容显示样式
/deep/.el-table {
  border: 0;
  // width: calc(100% - 8px) !important;
  .ele-item .ele-content {
    border: 0;
    padding: 0;
  }
  .el-table__row:hover .el-input__inner {
    background-color: transparent;
  }
}
</style>



