<template>
  <el-tabs
    v-model="tabActive"
    :type="element.options.type"
    :tab-position="element.options.tabPosition"
    :class="{
      [element.options.customClass]: element.options.customClass ? true : false
    }"
    style="margin-bottom: 18px"
  >
    <el-tab-pane
      :key="item.name"
      :label="item.label"
      :name="item.name"
      v-for="item in element.tabs"
    >
      <template v-for="tab in item.list">
        <generate-col-item
          v-if="tab.type == 'grid'"
          :key="tab.key"
          :model.sync="dataModels"
          :element="tab"
          @input-change="onInputChange"
          :edit="edit"
        >
        </generate-col-item>

        <generate-tab-item
          v-else-if="tab.type == 'tabs'"
          :key="tab.key"
          :model.sync="dataModels"
          :element="tab"
          @input-change="onInputChange"
          :edit="edit"
        >
        </generate-tab-item>

        <generate-sheet-item
          v-else
          :key="tab.key"
          :models.sync="dataModels"
          :widget="tab"
          @input-change="onInputChange"
          :edit="edit"
        >
        </generate-sheet-item>
      </template>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import GenerateSheetItem from './GenerateSheetItem'
import GenerateColItem from './GenerateColItem'

export default {
  name: 'generate-tab-item',
  components: {
    GenerateSheetItem,
    GenerateColItem
  },
  props: ['element', 'model', 'edit'],
  data() {
    return {
      dataModels: this.model,
      tabActive: this.element.tabs.length ? this.element.tabs[0].name : ''
    }
  },
  methods: {
    onInputChange(value, field) {
      this.$emit('input-change', value, field)
    }
  },
  watch: {
    model: {
      deep: true,
      handler(val) {
        this.dataModels = this.model
      }
    },
    dataModels: {
      deep: true,
      handler(val) {
        this.$emit('update:model', val)
      }
    }
  }
}
</script>



