<template>
  <el-dialog
    title="账号设置"
    class="AccountSettingDialog"
    :center="true"
    :visible.sync="dialogVisible"
    :modal-append-to-body="false"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="closeDialog"
  >
    <!-- 账号设置 -->
    <el-form
      class="account-form"
      label-width="100px"
      :model="accountForm"
      ref="accountForm"
      :rules="rules"
    >
      <el-form-item label="用户名">
        <el-input
          autocomplete="off"
          value=""
          v-model="accountForm.userId"
          placeholder="请输入用户名称"
          readonly
        >
        </el-input>
      </el-form-item>
      <el-form-item label="旧密码" prop="oldpassword">
        <el-input class="no-autofill" v-model="accountForm.oldpassword" placeholder="请输入旧密码">
          <!-- <svg-icon slot="prefix"
                      icon-class="Icon-login-psd" /> -->
        </el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="newpassword">
        <el-input v-model="accountForm.newpassword" placeholder="请输入新密码"> </el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="confirmpassword" required="">
        <el-input v-model="accountForm.confirmpassword" placeholder="请再次输入新密码"> </el-input>
      </el-form-item>
      <el-form-item class="submit-box">
        <el-button class="login-btn" type="primary" @click="save('accountForm')">提交</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
export default {
  name: 'AccountSetting',
  data() {
    const validateIsAgreement = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入新密码'))
      } else if (value !== this.accountForm.newpassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      dialogVisible: true,
      accountForm: {
        userId: '',
        oldpassword: '',
        newpassword: '',
        confirmpassword: ''
      },
      rules: {
        oldpassword: [
          { required: true, message: '请输入旧密码', trigger: 'blur' },
          { min: 6, max: 8, message: '长度在 6 到 8 个字符', trigger: 'blur' }
        ],
        newpassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { min: 6, max: 8, message: '长度在 6 到 8 个字符', trigger: 'blur' }
        ],
        confirmpassword: [{ validator: validateIsAgreement, trigger: 'blur' }]
      }
    }
  },
  mounted() {
    console.log(this.$wsCache.get('userInfo'))
    // this.$nextTick(() => {
    //   console.log(this.$refs.oldpassword)
    //   this.$refs.oldpassword.attr('readonly', true)
    //   this.$refs.newpassword.attr('readonly', true)
    //   this.$refs.confirmpassword.attr('readonly', true)
    // })
    this.getAccount()
  },
  methods: {
    getAccount() {
      this.$api.account.findAccount().then((res) => {
        // console.log(res)
        if (res.IsSuccess) {
          const data = res.Data
          this.accountForm.userId = data.UserID
        }
      })
    },
    save(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const params = {
            OldPassword: this.accountForm.oldpassword,
            NewPassword: this.accountForm.newpassword,
            ConfirmNewPassword: this.accountForm.confirmpassword
          }
          this.$api.account.changePassword(params).then((response) => {
            // console.log(response)
            if (response.IsSuccess) {
              this.$api.login.logout().then((res) => {
                if (res.IsSuccess) {
                  this.$wsCache.delete('loginFlag')
                  this.$wsCache.delete('userInfo')
                  this.$wsCache.delete('selectCrew')
                  this.$router.push('/login')
                }
              })
            }
          })
        } else {
          return false
        }
      })
    },
    closeDialog() {
      this.$emit('hiddenAccountSettingDialog')
    }
  }
}
</script>

<style lang="less" scoped>
.AccountSettingDialog {
  /deep/.el-dialog__header {
    background-color: #f3f3f3;
    .el-dialog__title {
      color: #303133;
    }
    i {
      color: #909399;
    }
  }
}
.theme-dark .AccountSettingDialog {
  /deep/.el-dialog__header {
    background-color: transparent;
  }
}
.account-form {
  width: 60%;
  margin: 0 auto 0;
  position: relative;
  .el-form-item.submit-box {
    /deep/.el-form-item__content {
      // position: absolute;
      margin-left: 50% !important;
      transform: translateX(-50%);
    }
  }
}
</style>



