import { get, post, newPost } from '../axios'
import request from '../request'
import { coordinateHandler } from '@/utils/common'

export default {
  // 获取设备列表
  // getMchLists: function (params) {
  //   return post(params, '/GeneratorSet/FindGeneratorSetsV1?Page=1&PageSize=1999')
  //   // return post(params, '/GeneratorSet/FindGeneratorSetsV1?Page=1&PageSize=90')
  // },
  // 获取设备实时数据信息
  getsingleMchInfoRealtime: function (params) {
    return get(params, '/GeneratorSet/FindGeneratorSetNowData')
  },
  // 获取单个设备信息
  getsingleMchInfo: function (params) {
    return get(params, '/GeneratorSet/FindGeneratorSet')
      .then((value) => {
        // GPS 位置纠偏
        // GPS->BD-09 （谷歌GPS坐标转百度地图BD09坐标）
        const data = !!value && !!value.Data && value.Data
        if (data) {
          const newCoordinate = coordinateHandler.gps_bd_encrypt(data.Latitude, data.Longitude)
          data.Longitude = newCoordinate.Longitude
          data.Latitude = newCoordinate.Latitude
        }
        return value
      })
      .catch((reason) => {
        // terminate Promise
        throw reason
      })
  },
  // 设备记录-运行记录
  FindGeneratorStartStopRecords: function (params, urlparams) {
    return post(params, '/GeneratorSet/FindGeneratorStartStopRecords?id=' + urlparams.id)
  },
  // 导出运行记录
  GensetStartStopRecordExportV2: function (params) {
    return request({
      url: `/Excel/GensetStartStopRecordExportV2?ExGenId=${params.mchCode}&StartTime=${params.starttime}&EndTime=${params.endtime}`,
      method: 'post',
      data: null,
      headers: {
        'Content-Type': 'application/json;charset=UTF-8'
      },
      responseType: 'blob'
    })
  },
  // 获取报警/保养信息
  FindGeneratorSetRadioIndexPage: function (params) {
    return post(params, '/GeneratorSet/FindGeneratorSetRadioIndexPage')
  },
  // 获取历史燃油曲线
  FindGeneratorSetHistoryDataV3: function (body, params) {
    return post(body, '/GeneratorSet/FindGeneratorSetHistoryDataV3?id=' + params.id)
  },
  // 获取用户图片和视频
  FindGeneratorSetByMonitor: function (params) {
    return get(params, '/GeneratorSet/FindGeneratorSetByMonitor')
  },

  // 查询机组维保策略以及提醒
  GetMaintenanceGensetSettingDetailV2: function (params, query) {
    return newPost(params, query, '/Maintenance/GetMaintenanceGensetSettingDetailV2')
  },
  // 机组维保策略优化-维保事项添加、删除
  EditMaintenanceGensetSettingV2: function (params, query) {
    return newPost(params, query, '/Maintenance/EditMaintenanceGensetSettingV2')
  }
}



